.back-grad {
  position: relative;
  border-radius: 0 0 12px 12px;
  background: url(../img/h.svg) no-repeat center top, linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
}

.back-grad::after {
  content: "";
  height: 10px;
  border-radius: 0 0 12px 12px;
  position: absolute;
  bottom: -10px;
  left: 20px;
  right: 20px;
  background: #cacadb;
}

.back-grad::before {
  content: "";
  height: 10px;
  border-radius: 0 0 12px 12px;
  position: absolute;
  bottom: -20px;
  left: 40px;
  right: 40px;
  background: #e7e7f2;
}

.header {
  overflow: hidden;
}
.header__top {
  background: rgba(245, 245, 250, 0.2);
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
}
.header__logo {
